body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: whitesmoke;
  max-width: 100%;

  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.base {
  height: 100%;
  background: black;
}

.home {
  height: 500px;
}

.particle {
  position: absolute;
  height: 500px;
  width: 98%;
  top: 10px;
}

.sayantan {
  padding-top: 175px;
  font-size: 2em;
  text-align: center;
}

.skill {
  text-align: center;
}

.menu {
  position: fixed;
  top: 0px;
  right: 0px;
  margin-left: 5em;
  width: 100%;
}

.image {
  height: 300px;
  margin-left: 20px;
}

.card {
  max-width: 18rem;
  background-color: black;
}

.menubackground {
  background-color: rgba(5, 1, 32, 0.80);
}

.howdy {
  margin-left: 35%;
}

a {
  color: none;
  text-decoration: none;
  /* no underline */
}